<template>
  <div>
    <Header :page-rolling="pageRolling"/>

    <el-container style="text-align: center">
      <!--背景图片-->
      <div style="z-index: -1;position: relative;margin: 0 auto">
        <el-image style="width: 100%; height: 100%;opacity: 0.9"
                  src="../../static/img/homeImg.jpg"
                  fit="fill"/>
      </div>
      <!--搜索框-->
      <div style="width: 100%;z-index: 999;position: absolute;">
        <el-input placeholder="" v-model="inputText"
                  class="input-with-select"
                  style="width: 40%;margin-top: 10%;"
                  type="text"
                  :clearable="true"
                  size="large">
          <el-select v-model="value" slot="prepend" placeholder="城市" popper-class="selectOptions" filterable>
            <!--分组展示数据,暂时不用,以后可能会用-->
            <!--            <el-option-group-->
            <!--              v-for="group in options"-->
            <!--              :key="group.label"-->
            <!--              :label="group.label">-->

            <!--              <el-option-->
            <!--                v-for="item in group.options"-->
            <!--                :key="item.value"-->
            <!--                :label="item.label"-->
            <!--                :value="item.value">-->
            <!--              </el-option>-->

            <!--            </el-option-group>-->
            <el-option
              v-for="cityName in cityNames"
              :key="cityName.cityId"
              :value="cityName.cityCnName">
            </el-option>

          </el-select>


          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
      </div>
    </el-container>

    <br/>

    <el-container style="display:flex;flex-direction: column">
      <p style="font-family: DengXian_Reg,serif;font-weight: bold;font-size: xx-large;text-align: center">推荐房源</p>
      <!--根据总数据长度决定有多少行-->
      <el-row type="flex" justify="center" class="row-bg" v-for="(item,index) in parseInt(zufangDataLength/3)+1"
              :key="item"
              align="middle"
              style="margin-top: 2%">
        <!--每行三个数据-->
        <el-col v-if="index <= zufangDataLength" :span="5" v-for="(item, index) in 3" :key="item"
                style="margin-left: 2%;margin-right: 2%">
          <!--示例数据-->
          <!--          <el-card :body-style="{ padding: '0px' }">-->
          <!--            <img src="../../static/img/test_1.jpg"-->
          <!--                 class="image" alt="">-->
          <!--            <div style="padding: 14px;">-->
          <!--              <span>测试</span>-->
          <!--              <div class="bottom clearfix">-->
          <!--                <time class="time">{{ currentDate |timeFormater}}<br/></time>-->
          <!--              </div>-->
          <!--            </div>-->
          <!--          </el-card>-->
          <el-card :body-style="{ padding: '0px' }">
            <img :src="zufangData[index].pictureUrl" class="image" alt="">
            <div style="padding: 14px;">
              <span>{{zufangData[index].name}}</span>
              <div class="bottom clearfix">
                <time class="time">{{zufangData[index].price}}<br/></time>
              </div>
            </div>
          </el-card>

        </el-col>
      </el-row>
    </el-container>

    <Footer/>
  </div>
</template>

<script>
  // @ is an alias to /src
  // import HelloWorld from '@/components/HelloWorld.vue'
  import Header from '../components/Header'
  import Footer from '../components/Footer'

  export default {
    name: 'home',
    data: function () {
      return {
        activeIndex: 1,
        pageRolling: 0,
        inputText: '',
        cityNames: [
          // {'cityCnName': '上海', 'cityId': '001'},
          // {'cityCnName': '北京', 'cityId': '002'},
          // {'cityCnName': '广州', 'cityId': '003'},
          // {'cityCnName': '合肥', 'cityId': '004'}
        ],
        //用户选中的城市名称
        value: '',
        zufangData: [
          {
            id: '001',
            name: '房屋名称',
            rentalType: '房屋出租方式',
            orientation: '房屋朝向',
            area: '房屋面积',
            price: '￥800/月',
            pictureUrl: '../../static/img/test_1.jpg'
          }
        ],
        zufangDataLength: 0,
        currentDate: new Date()
      }
    },
    //前端用
    components: {Footer, Header},
    methods: {
      scrollToTop () {
        /* eslint-disable */
        this.pageRolling = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      }
    },
    mounted () {
      window.addEventListener('scroll', this.scrollToTop)
    },
    created () {
      const that = this
      //请求有房源的城市列表
      that.$http.get('/citys', {
        params: {}
      })
        .then(function (response) {
          if (200 === response.data.errorCode) {
            // [{"cityCnName": "城市中文名称","cityId": "城市id"}]
            that.cityNames = response.data.data
          }
        })
        .catch(function (error) {
          console.log(error)
        })
        .then(function () {
          // always executed
        })
      //请求随机房源简略信息
      that.$http.get('/zufang', {
        params: {}
      })
        .then(function (response) {
          if (200 === response.data.errorCode) {
            // [{"id": "房屋标识id", "name": "房屋名称", "rentalType": "房屋出租方式", "orientation": "房屋朝向", "area": "房屋面积", "price": "房屋价格", "pictureUrl": "房屋展示图片url"}]
            //将获取到的数据写入租房数据中并渲染
            that.zufangData = response.data
            that.zufangDataLength = response.data.length
          }
        })
        .catch(function (error) {
          console.log(error)
        })
        .then(function () {
          // always executed
        })

    },

    filters: {
      timeFormater (e) {
        const year = e.getFullYear()
        const month = (e.getMonth() + 1).toString().padStart(2, '0')
        const day = e.getDate().toString().padStart(2, '0')
        return `${year}-${month}-${day}`
      }
    }
  }
</script>
<style>
  /*去除 a 标签的下划线*/
  a {
    text-decoration: none;
  }

  a:link {
    text-decoration: none; /* 指正常的未被访问过的链接*/

  }

  a:visited {
    text-decoration: none; /*指已经访问过的链接*/

  }

  a:hover {
    text-decoration: none; /*指鼠标在链接*/
  }

  a:active {
    text-decoration: none; /* 指正在点的链接*/

  }

  .header {
    position: relative;
    width: 100%;
    z-index: 999;
  }

  .header-active {
    /*background-color: rgba(255, 255, 255, 1);*/
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 999;
  }

  .el-footer {
    /*color: #333;*/
    text-align: center;
    line-height: 60px;
  }

  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    /*line-height: 3000px;*/
  }

  body > .el-container {
    margin-bottom: 40px;
  }

  /*搜索框左边选择模块的大小*/
  .input-with-select .el-input-group__prepend {
    background-color: #fff;
    width: 10%;
    font-size: smaller;
    text-align: left;
    font-family: FangSong, serif;
  }

  /*搜索框左边选择模块展开的页面宽度*/
  .selectOptions {
    width: 5%;
  }

  .time {
    font-size: 13px;
    /*color: #999;*/
    color: red;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }
</style>



