<template>
  <div>
    <el-menu
      class="el-container headerNav"
      :style="headerNavStyle"
      default-active="1"
      mode="horizontal"
      :router="true"
      active-text-color="#FFA640"
      ref="headerNav">
      <el-menu-item class="el-col-1 el-col-push-1" index="0" route="/">这是一个LOGO</el-menu-item>
      <el-menu-item class="el-col-1 el-col-push-19" index="1" route="/">首页</el-menu-item>
      <el-menu-item class="el-col-1 el-col-push-19" index="2" route="/404">介绍</el-menu-item>
      <el-menu-item v-if="!isLogin" class="el-col-1 el-col-push-19" index="3" route="/"
                    @click="loginDialogVisible = true">登录
      </el-menu-item>
      <el-menu-item v-if="!isLogin" class="el-col-1 el-col-push-19" index="4" route="/"
                    @click="RegisterDialogVisible = true">注册
      </el-menu-item>
      <el-menu-item v-if="isLogin" class="el-col-1 el-col-push-19" index="5"
                    route="/"
                    @click="$router.push({path: '/user' + '/' + userData.userPhone + '/' + userData.userName})">
        {{userData.userName}}
      </el-menu-item>
      <el-menu-item v-if="isLogin" class="el-col-1 el-col-push-19" index="6"
                    route="/"
                    @click="logout">
        退出
      </el-menu-item>

    </el-menu>

    <!--登录对话框-->
    <div>
      <el-dialog title="登录" :visible.sync="loginDialogVisible" center width="25%">
        <p style="text-align: center">没有账号?去
          <el-link type="info" @click="gotoRegister">注册</el-link>
        </p>
        <el-form :model="userLoginInfo" status-icon :rules="loginCheckRules" ref="userLoginInfo"
                 :label-position="labelPosition" label-width="100px">
          <el-form-item label="手机号/用户名" prop="name">
            <el-input v-model.trim="userLoginInfo.name"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input type="password" v-model.trim="userLoginInfo.password"></el-input>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="loginDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="doLogin('userLoginInfo')">登 陆</el-button>
        </div>
      </el-dialog>
    </div>

    <!--注册对话框-->
    <div>
      <el-dialog title="注册" :visible.sync="RegisterDialogVisible" center width="35%">
        <p style="text-align: center">已有账号?去
          <el-link type="info" @click="gotoLogin">登陆</el-link>
        </p>

        <el-form :model="userRegisterInfo" status-icon :rules="registerCheckRules" ref="userRegisterInfo"
                 label-width="100px">
          <el-form-item label="手机号" prop="phoneNumber">
            <el-input type="text" v-model.trim="userRegisterInfo.phoneNumber" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="验证码" prop="VerificationCode">
            <el-input v-model.number.trim="userRegisterInfo.VerificationCode" maxlength="6" show-word-limit clearable>
              <el-button slot="append" @click="getVerificationCode('userRegisterInfo')"
                         :disabled="disableGetVerificationCode">
                {{leftTime}}
              </el-button>
            </el-input>
          </el-form-item>
          <el-form-item label="用户名" prop="name">
            <el-input type="text" v-model.trim="userRegisterInfo.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input type="password" v-model.trim="userRegisterInfo.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkPass">
            <el-input type="password" v-model.trim="userRegisterInfo.checkPass" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>

        <div slot="footer" class="dialog-footer">
          <el-button @click="RegisterDialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="doRegister('userRegisterInfo')">注 册</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>

  export default {
    name: 'Header',
    props: ['PageRolling'],
    data: function () {
      const validateLoginName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入用户名或手机号!'))
        } else {
          callback()
        }
      }
      const validateLoginPassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入登录密码!'))
        } else {
          callback()
        }
      }
      //手机号码验证规则(不检验是否已用,仅检验是否为正常的大陆手机号)
      const validatePhoneNumber = (rule, value, callback) => {
        const that = this
        if (!value) {
          // this.disableGetVerificationCode = true
          return callback(new Error('手机号不能为空!'))
        }
        setTimeout(() => {
          if (value.length < 11 || !/^(1[35789]\d{9})$/.test(value)) {
            // this.disableGetVerificationCode = true
            callback(new Error('请输入正确的11位中国大陆手机号!'))
          } else {
            that.$http.post('/check/' + value, that.$qs.stringify({
              userPhone: value
            }), {headers: {'Content-Type': 'application/x-www-form-urlencoded'}})
              .then(function (response) {
                // console.log(response)
                if (response.data.errorCode === 200) {
                  // that.disableGetVerificationCode = false
                  callback()
                } else {
                  return callback(new Error(response.data.errorMsg))
                }
              })
              .catch(function (error) {
                return callback(new Error('检测手机号是否可用错误,请稍后重试!'))
                // that.disableGetVerificationCode = false
                // callback()
              })
          }
        }, 500)
      }
      //检验用户名是否已用,请求后端接口
      const validateName = (rule, value, callback) => {
        const that = this
        if (value === '') {
          callback(new Error('请输入用户名!'))
        } else {
          setTimeout(() => {
            that.$http.post('/check/' + value, that.$qs.stringify({
              userName: value
            }), {headers: {'Content-Type': 'application/x-www-form-urlencoded'}})
              .then(function (response) {
                // console.log(response)
                if (response.data.errorCode === 200) {
                  // that.disableGetVerificationCode = false
                  callback()
                } else {
                  return callback(new Error(response.data.errorMsg))
                }
              })
              .catch(function (error) {
                return callback(new Error('检测用户名是否可用错误,请稍后重试!'))
                // that.disableGetVerificationCode = false
                // callback()
              })
          }, 500)
        }
      }
      //密码验证规则
      const validatePassword = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码!'))
        } else {
          setTimeout(() => {
            if (value.length < 6 || -1 === value.search(/[a-z|A-Z]/) || -1 === value.search(/\d/) || -1 === value.search(/[@#$]/)) {
              callback(new Error('密码需六位以上,包含字母、数字及“@“、”#”、“$”中的一个'))
            }
            callback()
          }, 600)
        }
      }
      //二次输入密码规则检验
      const validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码!'))
        } else if (value !== this.userRegisterInfo.password) {
          callback(new Error('两次输入密码不一致!'))
        } else {
          callback()
        }
      }
      //验证码验证规则
      const validateCOde = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入六位验证码!'))
        } else if (6 !== value.length) {
          callback(new Error('请输入正确的六位验证码!'))
        } else {
          callback()
        }
      }

      return {
        //header 的样式,随下滑而改变
        headerNavStyle: '',
        //登录弹出框,默认为不弹出
        loginDialogVisible: false,
        //注册弹出框,默认为不弹出
        RegisterDialogVisible: false,
        //登录、注册弹出框的 label 位置,在输入框的上方,可选值:top left(左对齐) right(右对齐)
        labelPosition: 'top',
        //登录信息
        userLoginInfo: {
          //用户名或手机号
          name: '',
          //密码
          password: ''
        },
        //注册信息
        userRegisterInfo: {
          //手机号
          phoneNumber: '',
          //用户名
          name: '',
          //密码
          password: '',
          //再次输入密码
          checkPass: '',
          //验证码
          VerificationCode: ''
        },
        leftTime: '获取验证码',
        disableGetVerificationCode: false,
        //登录表单检验规则
        loginCheckRules: {
          name: [
            {validator: validateLoginName, trigger: 'blur'}
          ],
          password: [
            {validator: validateLoginPassword, trigger: 'blur'}
          ]
        },
        //注册表单检验规则
        registerCheckRules: {
          phoneNumber: [
            {validator: validatePhoneNumber, trigger: 'blur'}
          ],
          VerificationCode: [
            {validator: validateCOde, trigger: 'blur'}
          ],
          name: [
            {validator: validateName, trigger: 'blur'}
          ],
          password: [
            {validator: validatePassword, trigger: 'blur'}
          ],
          checkPass: [
            {validator: validatePass2, trigger: 'blur'}
          ]
        },
        //用户是否已经登录,默认为未登录
        isLogin: false,
        //用户个人信息
        userData: {
          //用户头像url
          userHead: '',
          // 昵称
          userName: '麦田里的风',
          // 手机号
          userPhone: '19927457507',
          // 是否学生认证
          studentAuthentication: false
        },
        loginCookie: ''
      }
    },
    watch: {
      //监控页面滑动使 header 样式发生改变
      PageRolling (newValue, oldValue) {
        const that = this
        // console.log(newValue)
        if (0 === newValue) {
          that.headerNavStyle = ''
        } else {
          that.headerNavStyle = '' +
            'background: rgba(255,255,255,0.8);' +
            'position: fixed;\n' +
            'top: 0;\n' +
            'width: 100%;\n' +
            'z-index: 999;'
        }
      }
    },
    methods: {
      //获取验证码
      getVerificationCode (formName) {
        const that = this
        //检验手机号是否填写正确
        that.$refs[formName].validateField('phoneNumber', function (errorMessage) {
          if (errorMessage === '') {
            that.leftTime = '发送中...'
            that.$http.get('/code', {
              params: {
                phone: that.userRegisterInfo.phoneNumber
              }
            })
              .then(function (response) {
                if (200 === response.data.errorCode) {
                  //禁用获取验证码按钮并开始倒计时,倒计时结束后停止禁用按钮
                  that.disableGetVerificationCode = true
                  let leftTime = 60
                  const intervalID = setInterval(() => {
                    if (leftTime > 0) {
                      that.leftTime = '还剩' + leftTime-- + '秒'
                    } else {
                      that.leftTime = '获取验证码'
                      that.disableGetVerificationCode = false
                      clearInterval(intervalID)
                    }
                  }, 1000)
                }
              })
              .catch(function (error) {
                console.log(error)
              })
              .then(function () {
                // always executed
              })

          }

        })
      },
      //执行登录请求
      doLogin (formName) {
        const that = this
        that.$refs[formName].validate((valid) => {
          if (valid) {
            //关闭登录弹窗
            that.loginDialogVisible = false

            //加载动画
            const loading = this.$loading({
              lock: true,
              text: 'Loading',
              background: 'rgba(245,245,245,0.7)'
            })

            that.$http.post('/login', that.$qs.stringify({
              userName: that.userLoginInfo.name,
              userPassword: that.userLoginInfo.password
            }), {headers: {'Content-Type': 'application/x-www-form-urlencoded'}})
              .then(function (response) {
                if (response.data.errorCode === 200) {
                  //登录成功,保存令牌到 cookie (有效期: 1 小时)
                  that.$cookie.set('token', response.data.data.token, {expires: '1h'})
                  //同时用 cookie 请求用户信息
                  that.$http.get('/i', {
                    params: {
                      Cookie: that.$cookie.get('token')
                    }
                  })
                    .then(function (response) {
                      if (200 === response.data.errorCode) {
                        //将请求到的数据写到用户信息中
                        that.userData = response.data.data
                        //设置为已登录
                        that.isLogin = true
                        //结束动画
                        loading.close()
                      }
                    })
                    .catch(function (error) {
                      console.log(error)
                    })
                    .then(function () {
                      // always executed
                    })
                } else {
                  //结束动画
                  loading.close()
                  alert(response.data.errorMsg)
                }
              })
              .catch(function (error) {
                //结束动画
                loading.close()
                alert(error)
                // console.log(error)
              })
          }
        })
      },
      //执行注册请求
      doRegister (formName) {
        const that = this
        that.$refs[formName].validate((valid) => {
          if (valid) {
            //关闭弹出框(不销毁元素)
            this.RegisterDialogVisible = false
            //加载动画
            const loading = this.$loading({
              lock: true,
              text: 'Loading',
              background: 'rgba(245,245,245,0.7)'
            })

            //向服务器提交请求
            that.$http.post('/register', that.$qs.stringify({
              userPhone: that.userRegisterInfo.phoneNumber,
              userName: that.userRegisterInfo.name,
              userPassword: that.userRegisterInfo.password
            }), {headers: {'Content-Type': 'application/x-www-form-urlencoded'}})
              .then(function (response) {
                loading.close()
                console.log(response)
                if (response.data.errorCode === 200) {
                  //注册成功,此时应当跳转到个人页面
                  alert('注册成功')
                } else {
                  loading.close()
                  alert(response.data.errorMsg)
                }
              })
              .catch(function (error) {
                loading.close()
                alert(error)
                // console.log(error)
              })
          } else {
            // console.log('请完善信息')
            return false
          }
        })
      },
      //打开注册对话框,关闭登录对话框
      gotoRegister () {
        this.RegisterDialogVisible = true
        this.loginDialogVisible = false
      },
      //打开登录对话框,关闭注册对话框
      gotoLogin () {
        this.loginDialogVisible = true
        this.RegisterDialogVisible = false
      },
      // 退出登录
      logout () {
        this.$cookie.delete('token')
        this.userData = ''
        this.isLogin = false
        this.$router.push('/reflushHome')
      }
    },
    //创建时检测本地是否有 cookie ,如果存在则请求用户信息并设置为已登录
    created () {
      const that = this
      //如果存在且未过期
      if (that.$cookie.get('token') !== null) {
        //请求用户信息并设置为已登录
        that.$http.get('/i', {
          params: {
            Cookie: that.$cookie.get('token')
          }
        })
          .then(function (response) {
            if (200 === response.data.errorCode) {
              //将请求到的数据写到用户信息中
              that.userData = response.data.data
              //设置为已登录
              that.isLogin = true
            }
          })
          .catch(function (error) {
            console.log(error)
          })
          .then(function () {
            // always executed
          })
      }
    },
    components: {}
  }
</script>

<style scoped>
  a {
    text-decoration: none;
  }

  a:link {
    text-decoration: none; /* 指正常的未被访问过的链接*/

  }

  a:visited {
    text-decoration: none; /*指已经访问过的链接*/

  }

  a:hover {
    text-decoration: none; /*指鼠标在链接*/
  }

  a:active {
    text-decoration: none; /* 指正在点的链接*/

  }

  .headerNav {

  }
</style>



