<template>
  <div>
    <div v-for="zufang in zufangData">
      {{zufang.id}}
      {{zufang.name}}
      {{zufang.rentalType}}
      {{zufang.orientation}}
      {{zufang.area}}
      {{zufang.price}}
      {{zufang.pictureUrl}}
    </div>
    <button @click="getCookie">获取cookie</button>
  </div>
</template>

<script>
  export default {
    name: 'test',
    data () {
      return {
        zufangData: [
          {
            id: '',
            name: '',
            rentalType: '',
            orientation: '',
            area: '',
            price: '',
            pictureUrl: ''
          }
        ]
      }
    },
    methods: {
      getCookie () {
        const response = JSON.parse('{\n' +
          '\t"errorCode": "返回码，正确为200，错误则为对应的错误码",\n' +
          '\t"errorMsg": "错误信息，返回正确时返回为空",\n' +
          '\t"data": [\n' +
          '\t\t{\n' +
          '\t\t\t"id": "房屋标识id",\n' +
          '\t\t\t"name": "房屋名称",\n' +
          '\t\t\t"rentalType": "房屋出租方式",\n' +
          '\t\t\t"orientation": "房屋朝向",\n' +
          '\t\t\t"area": "房屋面积",\n' +
          '\t\t\t"price": "房屋价格",\n' +
          '\t\t\t"pictureUrl": "房屋展示图片url"\n' +
          '\t\t}\n' +
          '\t]\n' +
          '}')
        console.log(response.data)
        this.zufangData = response.data
        console.log(this.zufangData)
      }
    }
  }
</script>

<style scoped>

</style>



