import Vue from 'vue'
import Router from 'vue-router'
// import Home from '@/components/Home'
import Home from '../views/Home'
import test from '../views/test'

Vue.use(Router)

const that = this

//在这里配置路径
const routes = [
  {
    path: '/test',
    name: 'test',
    component: test,
    meta: {
      title: 'test'
    }
  },
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title: 'wehouse'
    }
  },
  {
    path: '/reflushHome',
    name: 'reflushHome',
    component: () => import('../components/reflushHome.vue'),
    meta: {
      title: ''
    }
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
    meta: {
      title: '关于wehouse'
    }
  },
  {
    path: '/user/:userPhone/:userName',
    name: 'user',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "i" */ '../views/userdata.vue'),
    meta: {
      title: ''
    }
  },

  { /* Not Found 路由，必须是最后一个路由 */
    path: '*',
    component: () => import(/* webpackChunkName: "404" */ '../views/404.vue'),
    meta: {
      title: '找不到页面'
    }
  }
]

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

//动态设置页面标题
router.beforeEach((to, from, next) => {
  //如果前往个人页面,那么把标题设置为用户名
  if (to.path.match(/\/user\/\d{11}\/.*/)) {
    window.document.title = 'wehouse | ' + to.path.split('/')[3]
  } else {
    window.document.title = to.meta.title
  }
  next()
})

export default router



